// 텍스트 네비게이션 추가 함수
function addNavigationText() {
    var navigationBullets = document.querySelectorAll('.swiper-pagination-bullet');

    navigationBullets.forEach(function (bullet, index) {
        var textElement = document.createElement('span');
        textElement.className = 'swiper-pagination-text';
        textElement.textContent = 'Slide ' + (index + 1);

        bullet.appendChild(textElement);
    });
}

// 텍스트 네비게이션 추가 호출
addNavigationText();
